@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed|Roboto:400,700);
html,
body {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff; }

.container {
  display: grid;
  grid-template-rows: 35px auto 50px;
  grid-template-areas: 'header' 'content' 'footer';
  height: 100vh; }

.template {
  display: grid;
  grid-area: content;
  background-color: #757575;
  grid-template-areas: 'title' 'message' 'inputs'; }
  .template .title {
    grid-area: title;
    background-color: yellow; }
  .template .message {
    grid-area: message;
    background-color: blue; }
  .template .inputs {
    grid-area: inputs;
    background-color: green; }

.header {
  display: grid;
  grid-area: header;
  background-color: #f44336;
  grid-template-areas: 'left right';
  padding: 0px 8px; }
  .header .left {
    grid-area: left;
    align-self: center;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase; }
    .header .left a {
      color: #ffffff;
      text-decoration: none; }
  .header .right {
    grid-area: right;
    align-self: center;
    justify-self: end; }

.footer {
  display: grid;
  grid-area: footer;
  background-color: #f44336; }
  .footer .text {
    justify-self: center;
    align-self: center; }

